a{
  text-decoration: none;

}
.none{
  display: none;
}
.block{
  display:block;
}
/*  */

.project_title h1{
  font: 2em;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 200px;
}
.project_container{
  margin:0px 20px;
  /* height: 70vh; */
  display: flex;
  align-items: top;
}
.container_img{
  flex: 1;
  text-align: center;
  position: relative;
}
.img_btn{
  position:absolute;
  opacity: 0.4;
  
}
.container_img button:first-child{
  top:50%;
  left: 0%;
}
.container_img button:nth-child(2){
  top: 50%;
  right: 0%;
}
.container_img img{
  width: 80%;
}

.container_desc{
  flex: 1;
  font-size: 1.3em;
}
.container_desc p:nth-child(2){
  margin-bottom:30px;
  border-bottom: 1px solid black;
}
.container_desc p:nth-child(n+3){
  margin-bottom:10px;
}
.overbtn{
  width: 100px;
  height: 30px;
  position: absolute;
  top: 50%;
  border-radius: 20px;
}
.overbtn:hover{
  background-color: rgba(0, 0, 0, 0.3);
  color: black;
  background-color: white;
}
.btnL{
  left: 50%;
  top:50%;
  transform: translate(-50%,-50%);
  background-color: pink;
}
.btnR{
  left: 50%;
  background-color: yellowgreen;
}
@media screen and (max-width:900px) {
  .project_container{
    display: block;
  }
  .container_desc p:first-child{
    margin-top: 80px;
  }

  .project_title h1{
    margin: 50px 0px;
  }
  .overbtn{
    width: 70px;
    height: 20px;
  }

  
}
