/* 공통 CSS */

.tech{
  width: 95vw;
  height: 100%;
  margin: 20px auto;
}

.tech_title{
  margin: 50px;
  font-size: 1.5rem;
  font-weight: bolder;
  text-align: center;
}

.tech_graph h1{
  font-size: 1rem;
}
.tech_graph{
  flex-basis: 70%;
}
.tech_container{
  display: flex;
  justify-content: left;
}
.container_item{
  margin: 30px 30px;
  position: relative;
}
.item_grahp{
  background-color: black;
  border-radius: 30px;
  animation: graph 2.5s 1;
}
@keyframes graph{
  0%{
    width: 0%;
    opacity: 0;
  }
}
.item_grahp div{
  text-align: right;
  margin-right: 30px;
  color: white;
}

.item_title{
  margin-bottom: 10px;
  cursor: pointer;
}

.tech_desc{
  flex-basis: 30%;
  margin-left: 50px;
  animation: fade 2.5s;
}

.tech_desc p:nth-child(1){
  font-size: 4em;
  margin-bottom: 40px;
}
.tech_desc p:nth-child(n+2){
  font-size:1.5em;
  margin:20px 0px;
}

.container_item:hover .item_grahp{
  color:black;
}

.clicknotion:hover{
  color:red;
  cursor:pointer;
}
.item_title h1:hover{
  color:red;
  cursor:pointer;
}

@media screen and (max-width:900px) {
  .tech_container{
    display: block;
  }

  .tech_desc{
    margin-top:50px;
  }
  .tech_desc{
    margin-left: 0px;
  }
}

@keyframes fade{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}