.login{
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url('../imgs/login_img.avif');
  background-size: cover;
}
.login_container{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login_img img{
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.login_input input:first-child{
  width: 250px;
  height: 30px;
  border-radius: 80px;
  border:none;
  margin-right: 10px;
}