.f_red{
  color:red;
}
.f_bolder{
  font-weight: 900;
}
/* font style */
.end{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.end p{
  margin: 15px 0px
}
.end_content{
  margin:auto;
  /* flex:1; */
  font-size: 1.3em;
}
.blank_margin{
  flex-basis: 10%;
  height: 100vh;
  background-color: skyblue;
}

@media screen and (max-width:900px) {
  .end{
    display: block;
  }
  .blank_margin{
    height: 0vh;
  }
  .end_content{
    margin:30px 20px;
    font-size: 1.3em;
  }
}



